import axios from 'axios';
import React from 'react';

export default class ComponentWillMount extends React.PureComponent {
    constructor() {
			super();
        this.state = {
            name: '',
        }
    }

    UNSAFE_componentWillMount () {
        axios.get('/api/user', {
            maxRedirects: 0
        }).then(res => {
            console.log(res, 111111)
        }).catch(err => {
            console.log(err, '--------------');
        })
    }
    // TAG 

    render () {
        return (
            <div>class{this.state.name}</div>
        )
    }
}